import React, {useEffect, useState} from 'react';
import {useLocation, useParams} from "react-router-dom";
import {roleService} from "../../../services/roleService";
import {BookDoctorComponent} from "../../booking-doctor/BookDoctorComponent/BookDoctorComponent";
import './DoctorsDetailsComponent.css';



const DoctorsDetailsComponent = () => {
    const {id} = useParams();
    const roleId = id || localStorage.getItem("roleId");
    const [doctors, setDoctors] = useState(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState('');
    const location = useLocation();
    const searchParams = new URLSearchParams(location.search);
    const patientId = searchParams.get('patientId');
    const userId = searchParams.get('userId');



    useEffect(() => {
        const fetchDoctors = async () => {
            try {
                const res = await roleService.getProfileDoctorById(roleId);
                setDoctors(res.data);
            } catch (error) {
                setError('Doctor profile not found');
            } finally {
                setLoading(false);
            }
        };
        fetchDoctors();
    }, [roleId]);

    if (loading) return (
        <div className="doctor-profile-skeleton">
            <div className="doctor-profile-skeleton-avatar"></div>
            <div className="doctor-profile-skeleton-line short"></div>
            <div className="doctor-profile-skeleton-line long"></div>
            <div className="doctor-profile-skeleton-line long"></div>
        </div>
    );
    if (error) return <p style={{color: 'red'}}>{error}</p>;
    if (!doctors) return <p>No doctors data</p>;


    const {profile} = doctors.user;


    return (
        <div className={'doctors-details-component'}>
            <div className={'doctors-details-component-content'}>
                <h3>{profile.name} {profile.surname}</h3>
                <p><strong>Role:</strong> {doctors.role}</p>
                {doctors.specialty && <p><strong>Specialty:</strong> {doctors.specialty}</p>}
                {doctors.role === 'doctor' && (
                    <p><strong>Available for booking:</strong> {doctors.is_available_for_booking ? 'Yes' : 'No'}</p>
                )}
            </div>
            <div className={'doctors-details-component-book-doctor-component'}>
                {userId && (
                    <BookDoctorComponent id={userId} patientId={patientId}/>
                )}

            </div>
        </div>
    );
};

export {DoctorsDetailsComponent};